html {
  cursor: none;
}

ul,
ol,
h1,
h2,
h3,
a,
p {
  text-decoration: none;
  list-style: none;
  margin: 0;
  padding: 0;
}
#root {
  /* cursor: url(./images/cursor2.png) 53 53, auto; */
}
.swiper-pagination-bullet {
  cursor: pointer;
  height: 0.5rem;
  width: 3.5rem;
  margin: 0 3px;
  border-radius: 1%;
  background-color: rgba(151, 148, 148, 0.3);
  display: inline-block;
  transition: all 0.5s ease-out;
}

@keyframes swing {
  100% {
    transform: translateX(100%);
  }
}
@keyframes side {
  100% {
    transform: translateY(100%);
  }
}
@media screen and (max-width: 768px) {
  body::after {
    background-image: none;
  }
  html,
  body {
    cursor: auto;
  }
}
