.item {
  margin: 10px 20px;
  text-transform: uppercase;
  background: linear-gradient(
    45deg,
    rgba(14, 15, 15, 0.5),
    rgba(37, 30, 30, 0.5)
  );
  box-shadow: 8px 8px 10px rgb(22, 22, 22);
  border-radius: 4px;
}
.item:hover .navItem,
.item:focus .navItem {
  transform: scale(1.03);
}
.navItem {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "League Gothic";
  font-size: 2em;
  transition: all 0.25s ease;
  color: inherit;
  text-transform: uppercase;
}

.img {
  padding-top: 0.5em;
  object-fit: cover;
  width: 100%;
  height: 65vh;
}
.prev,
.next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  padding: 0.5rem 1rem;
  margin-top: -3rem;
  font-size: 3rem;
  transition: all 0.5s ease-out;
  z-index: 1;
}
.prev:hover,
.prev:focus,
.next:hover,
.next:focus {
  transform: scale(1.1);
  background-color: rgba(82, 76, 76, 0.5);
  border-radius: 8px;
  /* height: 10%; */
}
.prev {
  left: 0;
}
.next {
  right: 0;
}
.dots {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  top: 90%;
  justify-content: center;
  /* z-index: 101; */
}
.dot,
.activeDot {
  cursor: pointer;
  height: 0.5rem;
  width: 1.5rem;
  margin: 0 3px;
  background-color: rgba(151, 148, 148, 0.3);
  /* border-radius: 50%; */
  display: inline-block;
  transition: all 0.5s ease-out;
}
.dot:hover,
.dot:focus,
.activeDot {
  background-color: rgba(255, 255, 255, 0.5);
  transform: scale(1.1);
}
.sliderList {
  display: flex;
  justify-content: center;
}
